<template>
  <div id="app">
    <h1>test</h1>

    front<input type="text" v-model="fishFront"><br />
    back<input type="text" v-model="fishBack"><br />
    kategoria<input type="text" v-model="fishCategory"><br />
    temat<input type="text" v-model="fishChapter"><br />
    <button v-on:click="addFish()" type="button">Click Me!</button>

    <!-- List display of data
    <ul>
      <li v-for="fish of fishes" :key="fish.id">
        {{fish.front}}
      </li>
      <li  v-for="fish of fishes" :key="fish.id">
        {{fish.back}}
      </li>
    </ul>
-->

    <!-- Table display of data -->
    <table border="1px" class="tg" style="undefined;table-layout: fixed; width: 293px">
      <colgroup>
        <col style="width: 159px">
        <col style="width: 134px">
        <col style="width: 134px">
        <col style="width: 134px">
      </colgroup>
      <thead>
        <tr>
          <th class="tg-0lax">
            <li v-for="fish of fishes" :key="fish.id">
              {{fish.front}}
            </li>
          </th>
          <th class="tg-0lax">
            <li  v-for="fish of fishes" :key="fish.id">
              {{fish.back}}
            </li>
          </th>
          <th class="tg-0lax">
            <li  v-for="fish of fishes" :key="fish.id">
              {{fish.category}}
            </li>
          </th>
          <th class="tg-0lax">
            <li  v-for="fish of fishes" :key="fish.id">
              {{fish.chapter}}
            </li>
          </th>
        </tr>
      </thead>
    </table>





  </div>
</template>

<script>
import axios from 'axios';

const baseURL = 'http://localhost:3000/fishes'

export default {
  name: 'app',
  data() {
    return {
      fishes: [],
      fishFront: '',
      fishBack: '',
      fishCategory: '',
      fishChapter: ''
    }
  },
  async created() {
    try {
      const res = await axios.get(baseURL)

      this.fishes = res.data;
    } catch(e) {
      console.error(e)
    }
  },
  methods: {
    async addFish() {
      const res = await axios.post(baseURL, {
        front: this.fishFront,
        back: this.fishBack,
        category: this.fishCategory,
        chapter: this.fishChapter
        })

      this.fishes = [...this.fishes, res.data]

      this.fishFront = ''
      this.fishBack = ''
      this.fishCategory = ''
      this.fishChapter = ''
    },
  }
}
</script>

